.captionBumperrepairHeader {
    height: 750px;
    text-align: center;
    vertical-align: text-top;
    background-image: url("/public/assets/images/car/166.jpg");
    background-repeat: no-repeat;
    background-size: 100% 100%;
    text-shadow: 2px 2px 3px #000;
    animation: fill-images 30s;
    -webkit-animation: fill-images 30s;
    -moz-animation: fill-images 30s;
    transition: background-image 1s ease-in-out;
    animation-iteration-count: infinite;
  }

  .bumperrepair-caption {
    height: 750px;
    text-shadow: 2px 2px 3px #000;
    text-align: center;
    background-image: linear-gradient(rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2)),url("/public/assets/images/Car1/098.jpg");
    background-size: 100% 100%;
  }

  .bumperrepair-swiper{
    height: 750px;
  }

  .captionImages{
    width:100%;
    height:800px;
  }

  .get-free-estimate{
    background-size: cover;
  }

  .bumperrepair-redirect-btn{
    padding: 0.5rem 2rem;
    border: 1px #ccc solid;
    display: inline-block;
    margin: 2rem 0 1.4rem 0 !important;
    border-radius: 50px;
    text-decoration: none !important;
    color: #fff !important;
    box-shadow: 10px 10px 5px rgba(0, 0, 0, 0.37);
  }

  .bumperrepair-redirect-btn:hover{
    background: #f4f4f4;
   color: #c01a2b !important;
  }

  

 

@media (max-width:950px) {
    .captionBumperrepairHeader {
        height: 1000px;
        background-size:cover;
    }

   
    .bumperrepair-caption {
        height: 650px;

      }
    
    
}

@media (max-width:430px) {
  .logoSection{
    width:50% !important;
 }

 .captionBumperrepairHeader {
  height: 1300px;
  background-size:cover;
}

.captionImages{
  width:100%;
  height:300px;
}

.bumperrepair-swiper{
  height: 100%;
  width:100%;
  overflow:  hidden !important;
}
}